if (typeof window !== "undefined") {
  getCLS(console.log);
  getFID(console.log);
  getLCP(console.log);

  const Axe = require("@axe-core/react");
  Axe(React, 1000);
}

//FUNCTION TO GET API DATA

async function getAPI(){
const url = 'https://mocki.io/v1/b9c63035-97c5-40a0-b45c-2abdf5261bdf';
const response = await fetch(url);
const data = await response.json();

for (let i = 0; i < 6; i++){
document.getElementById('Feature' + (i + 1)).textContent = data[i].title;
  document.getElementById('Content' + (i + 1)).textContent = data[i].description;
  document.getElementById('Button' + (i + 1)).textContent = data[i].button.text;
  document.getElementById("Link" + (i + 1)).href = data[i].button.link;
  }
};

getAPI();

